.container {
    margin: 4% auto 8% auto;
    width: 80%;
}

.header {
    color: #803fafb7;
    background: linear-gradient(90deg, #4d2f602e, #2421289d, #484448bf, #130c12b6);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: contrast(1.5);

    text-align: center;
    font-size: 180%;
    font-weight: 500;
    margin-bottom: 4%;
}

.cards {
    display: grid;
    grid-template-columns: 1fr  1fr;
    column-gap: 7%;
}

.card {
    color: #d8b6fa;
    box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.61), 0 0 0 1px rgba(10, 10, 10, 0.123);
    background: linear-gradient(135deg, #0000006c, #22355783);
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    aspect-ratio: 10 / 12;
    border-radius: 10%;
    border: 1px solid #00000029;
    margin: 4%;
    overflow: hidden;
    transition: 700ms 100ms;
    z-index: 4;
}

.card:hover {
    transform: scale(1.4) rotate(-1deg);
    z-index: 5;
}

.img {
    width: 100%;
    border-bottom: 1px solid #00000054;
}

.text {
    padding: 2%;
    font-size: 80%;
}
.video_card{
    box-shadow:0 0.4em 1em -0.125em rgba(10, 10, 10, 0.61), 0 0 0 1px rgba(10, 10, 10, 0.123);
    box-sizing: border-box;
    aspect-ratio: 1 / 1;
    border-radius: 10%;
    border: 1px solid #00000054;
    margin: 4%;
    overflow: hidden;
}
.video{
    width: 100%;
    height: 100%;
    position: relative;
}
.loading_spinner {
    margin: 6vw auto;
    width: 12vw;
    height: 12vw;
    border: 4px solid transparent;
    border-top: 5px dashed #f3f3f3;
    border-radius: 50%;
    animation: loading_animation 1s linear infinite;
}

@keyframes loading_animation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
} 
