button{
    background: transparent;
    background-color: transparent;
    border: none;
}
.container{
    padding-top: 5%;
    border-top: 2px solid #00000045;
    margin-top: 10%;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    display: flex;
    flex-direction: column; 
}
.input{
    display: flex;
    flex-direction: column;
    margin-bottom: 5%;
}
.text{
    color: #000000ce;
    resize: none;
    height: 12vh;
    border: 2px solid #04162135;
    border-radius: 5px; 
    background-color: rgba(202, 229, 220, 0.443);
}
.name_btn{
    margin-top: 5%;
    display: flex;
    justify-content: space-between;
}
.name{
    color: #000000ce;
    border: 2px solid #04162135;
    border-radius: 5px; 
    background-color: rgba(202, 229, 220, 0.392);
}
.button{
    color: #d8b6fa;
    font-weight: 900;
    box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.498), 0 0 0 1px rgba(63, 24, 79, 0.172);
    background: linear-gradient(135deg, #0000003c, #22355783);
    border-radius: 2px;
    text-decoration: none;
    transition: 300ms;
    font-size: 100%;
}
.button:hover{
    filter: brightness(0.9);
    transform: rotate(-1deg);
}
.button:active{
    transform: scale(1.05);
}
.text:focus-visible,
.name:focus-visible{
    outline: 2px solid #7316c54b;
    animation:  3s ease-in-out 0s infinite alternate backwards input;
    box-shadow: 0px 5px 20px -7px rgba(0, 0, 0, 0.777);
}
@keyframes input{
    0%{
        box-shadow: 0px 0px 10px -7px rgba(0, 0, 0, 0.777);
    }
    50%{
        box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.812);
    }
}
.comentError{
    margin-top: 5%;
    text-align: center;
    font-size: 80%;
    color: #e84b4b;
    font-weight: 900;
}
.coments{
    margin-top: 5%
}
.coment{
    color: #d8b6fa;
    box-shadow:0 0.4em 1em -0.125em rgba(10, 10, 10, 0.711), 0 0 0 1px rgba(10, 10, 10, 0.188);
    background: linear-gradient(135deg, #0000006c, #22355783);
    border-radius: 5px;
    padding: 1.5% 4% 1.5% 4%;
    margin-bottom: 8%;
    word-wrap: break-word;
}
.name_coment{
    font-size: 110%;
}
.text_coment{
    margin-bottom: 5%;
    margin-top: 5%;
    font-size: 95%;
    height: fit-content;
    
}
.meta_coment{
    display: flex;
    justify-content: space-between;
    font-size: 70%;
}
.liked{
    display: flex;
    align-items: center;
}
.like_coment{
    color: #0000009b;
    
}
.like_coment:hover{
    transform: scale(1.1);
}
.like_coment:active{
    color: #f506d19b;
}
.loading_spinner {
    margin: 6vw auto;
    width: 12vw;
    height: 12vw;
    border: 4px solid transparent;
    border-top: 5px dashed #f3f3f3;
    border-radius: 50%;
    animation: loading_animation 1s linear infinite;
}

@keyframes loading_animation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
