.container {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 10fr 3fr;
    align-items: center;
    text-align: center;
    margin-top: 7px;
    margin-bottom: 7px;
}

.link {
    background: linear-gradient(90deg, #2917349d, #552460, #3f2e3fbf, #480d3fdc);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: contrast(1.5);

    font-size: 100%;
    text-decoration: none;
    margin-right: 3vw;
    font-weight: 550;
    transition: 300ms;
}

.link:hover {
    background: linear-gradient(90deg, #51405ece, #594e65, #484448bf, #65305d);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    transform: rotate(1deg);
}

.img {
    width: 45px;
    height: 45px;
    border-radius: 5px;
    margin-inline: 2vw;
    box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.289), 0 0 0 1px rgba(10, 10, 10, 0.07);
    border: 1px solid #00000054;
    box-sizing: border-box;
    filter: brightness(90%);
    animation: 6s linear 0s infinite alternate backwards img;
}

@keyframes img {
    0% {
        box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.289), 0 0 0 1px rgba(10, 10, 10, 0.07);
    }

    10% {
        box-shadow: 0 0.4em 10px -0.125em rgba(10, 10, 10, 0.455), 0 0 10px 1px rgba(10, 10, 10, 0.175);
    }
}

.void {
    display: flex;
    z-index: -2;
    justify-content: center;
    gap: 2%;
    transform: scale(1.4);
}

.s {
    transform: translate(-2px, -1px) rotate(-2deg);
    animation: 1000ms ease-in-out 50ms infinite alternate backwards letters;
}

.i1 {
    transform: translate(0.3px, 1px) rotate(-2deg) scale(1.1);
    animation: 800ms ease-in-out 100ms infinite alternate backwards letters;
}

.n {
    transform: translateY(1px);
    animation: 700ms ease-in-out 34ms infinite alternate backwards letters;
}

.i2 {
    transform: translate(1px, 2px) rotate(-6deg) scale(1);
    animation: 900ms ease-in-out 0ms infinite alternate backwards letters;
}

.y {
    margin-right: 2%;
    transform: translate(1px, 2px) scale(1.1);
    animation: 1100ms ease-in-out 0ms infinite alternate backwards letters;
}

.v {
    transform: translate(1px, 0px) rotate(5deg) scale(1.1);
    animation: 1500ms ease-in-out 1000ms infinite alternate backwards letters;
}

.l {
    transform: translate(1px, 2px) rotate(-6deg) scale(1);
    animation: 2700ms ease-in-out 0ms infinite alternate backwards letters;
}

.a {
    transform: translateY(2px);
    animation: 900ms ease-in-out 0ms infinite alternate backwards letters;
}

.d {
    transform: translate(-2px, -1px) rotate(-2deg);
    animation: 900ms ease-in-out 0ms infinite alternate backwards letters;
}
.arrow{
    display: none;
}
@keyframes letters {
    0% {
        transform: translate(0px, 0px) rotate(0deg) scale(1);
    }

    100% {}
}

.menu_img {
    display: none;
    background: transparent;
    border: none;
    transition: 200ms;
}
.links2 {
    display: none;
}

@media(max-width: 576px) {
    .container {
        grid-template-columns: 1fr 1fr 6fr 1fr;
    }

    .links2{
        display: none;
    } 
    .links {
        display: flex;
        display: none;
    }
   .menu_img{
    display: block;
    z-index: 2;
    margin: 0;
    font-size: 150%;
    color: #00000054;
    font-weight: 1000;
    width: min-content;
    padding-inline: 13%;
    border: 2px solid #00000054;
    border-radius: 5px;
   }
   .menu_img:hover{
    color: #ae429c93;
    border: 2px solid #ae429c93;
    font-size: 160%;
   }
   .arrow{
    display: block;
    position: fixed;
    z-index: 30;
    font-size: 400%;
    color: #bd73c493;
    font-weight: 1000;
    filter: contrast(1.3);
    top:4%;
    right: 12%;
    animation: 3s linear 1s 1 normal forwards arroww;
   }
}
@keyframes arroww {
 0%{
     visibility: visible;
     filter: contrast(3);
     transform: scale(1.1);
 }
 15%{
    transform: scale(1);
     filter: contrast(0.5);
 }
 32%{
    filter: contrast(3);
    transform: scale(1.1);
}
45%{
    transform: scale(1);
    filter: contrast(0.5);
}
64%{
    filter: contrast(3);
    transform: scale(1.1);
}
83%{
    transform: scale(1);
    filter: contrast(0.5);
}
98%{
    transform: scale(1.1);
     filter: contrast(3);
    } 
    100%{
        visibility: hidden;
        display: none; 
 }
}