.container{
    margin-top: 4%;
    margin-bottom: 4%;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.header{
    color: #803fafb7;
    background: linear-gradient(90deg, #4d2f606e, #2421289d, #484448bf, #130c12b6);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: contrast(1.5);

    text-align: center;
    font-size: 180%;
    font-weight: 500;
    margin-bottom: 4%;
} 
.text{
    color: #693191b7;
    background: linear-gradient(90deg, #4d2f6090, #242128bb, #484448bf, #130c12b6);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: contrast(1.5);

    font-size: 110%;
    font-weight: 500;
    margin-top: 16%;
}
.input{
    border: 2px solid #04162135;
    border-radius: 5px;
    width: 16%;
    transform: scale(1.2);
    -webkit-text-security: square !important;
    background-color: rgba(202, 229, 220, 0.443);
    margin-top: 4%;
    margin-bottom: 16%;
}
.input:focus-visible{
    outline: 2px solid #5616c576;
    animation:  3s ease-in-out 0s infinite alternate backwards input;
    box-shadow: 0px 5px 20px -7px rgba(0, 0, 0, 0.777);
}
@keyframes input{
    0%{
        box-shadow: 0px 0px 10px -7px rgba(0, 0, 0, 0.777);
    }
    50%{
        box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.812);
    }
}
.loading_spinner {
    margin: 6vw auto;
    width: 12vw;
    height: 12vw;
    border: 4px solid transparent;
    border-top: 5px dashed #f3f3f3;
    border-radius: 50%;
    animation: loading_animation 1s linear infinite;
}

@keyframes loading_animation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
.error{
    text-align: center;
    font-size: 80%;
    color: #e84b4b;
    font-weight: 900;
}