.container{
    padding-top: 5%;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    display: flex;
    flex-direction: column; 
}
.header {
    color: #803fafb7;
    background: linear-gradient(90deg, #4d2f602e, #2421289d, #484448bf, #130c12b6);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: contrast(1.5);
    text-align: center;
    font-size: 180%;
    font-weight: 500;
    margin-bottom: 4%;
}
.gallery_input{
    padding: 5% 0 5% 0;
    border-top: 2px solid #00000045;
    display: flex;
    flex-direction: column;
    
}
.image{
    margin-bottom: 3%;
    color: #d8b6fa;
    font-weight: 900;
    box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.498), 0 0 0 1px rgba(63, 24, 79, 0.172);
    background: linear-gradient(135deg, #0000003c, #22355783);
    border-radius: 2px;
    text-decoration: none;
    transition: 300ms;
    font-size: 90%;
    width: min-content;
    margin-top: 3%;
    opacity: 0.6;
}
.text{
    margin-bottom: 1%;
    color: #000000ce;
    border: 2px solid #04162135;
    border-radius: 5px; 
    background-color: rgba(202, 229, 220, 0.392);
    width: 90%;
}
.button{
    color: #d8b6fa;
    font-weight: 600;
    box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.498), 0 0 0 1px rgba(63, 24, 79, 0.172);
    background: linear-gradient(135deg, #0000003c, #22355783);
    border-radius: 2px;
    text-decoration: none;
    transition: 300ms;
    font-size: 100%;
    width: fit-content;
    margin-top: 3%;
    margin-bottom: 3%;
    height: fit-content;
    padding-inline: 3%;
}
.button:hover,
.select:hover{
    filter: brightness(0.9);
    transform: rotate(-1deg);
}
.button:active,
.select:active{
    transform: scale(1.05);
}
.text:focus-visible,
.text_area:focus-visible{
    outline: 2px solid #7316c54b;
    animation:  3s ease-in-out 0s infinite alternate backwards input;
    box-shadow: 0px 5px 20px -7px rgba(0, 0, 0, 0.777);
}
@keyframes input{
    0%{
        box-shadow: 0px 0px 10px -7px rgba(0, 0, 0, 0.777);
    }
    50%{
        box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.812);
    }
}
.error{
    text-align: center;
    font-size: 80%;
    color: #e84b4b;
    font-weight: 900;
}
.select{
    color: #d8b6fa;
    font-weight: 900;
    box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.498), 0 0 0 1px rgba(63, 24, 79, 0.172);
    background: linear-gradient(135deg, #0000003c, #22355783);
    border-radius: 2px;
    text-decoration: none;
    transition: 300ms;
    font-size: 100%;
    width: min-content;
    margin-top: 1%;
    opacity: 0.9;
    outline: none;
    text-align: center;
}
.option{
    background: #22355783;
    color: #000000b5;
}
.option:hover{
    color: #d8b6fa;
    filter: contrast(1.1);
    background-color: #22355783;
}
.loading_spinner {
    margin: 6vw auto;
    width: 12vw;
    height: 12vw;
    border: 4px solid transparent;
    border-top: 5px dashed #f3f3f3;
    border-radius: 50%;
    animation: loading_animation 1s linear infinite;
}

@keyframes loading_animation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.structure_container{
    margin-top: 5%;
    width: 100%;
    margin-bottom: 5%;
}
.structure_section,
.q_structure_section,
.vip_structure_section{
    height: min-content;
    padding: 2% 0 2% 0;
    width: 100%;
    display: grid;
    position: relative;
    z-index: 1;
    gap: 3%;
    align-items: center;
    border-top: 2px solid #00000045;
}
.structure_section{
    grid-template-columns: 4fr 8fr 6fr 1fr 1fr;
}
.q_structure_section{
    grid-template-columns: 18fr 1fr 1fr;
}
.vip_structure_section{
    grid-template-columns: 4fr 14fr 1fr 1fr;
}
.structure_img{
    width: 100%;
    border-radius: 10%;
    border: 1px solid #00000029;
    transition: 700ms 100ms;
    z-index: 2;
    margin: auto;
}
.structure_img:hover{
    transform: scale(1.5);
    z-index: 3;
}
.structure_button{
    color: #d8b6fa;
    font-weight: 600;
    box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.498), 0 0 0 1px rgba(63, 24, 79, 0.172);
    background: linear-gradient(135deg, #0000003c, #22355783);
    border-radius: 2px;
    text-decoration: none;
    transition: 300ms;
    font-size: 100%;
    padding: 10%;
    padding-inline: 1vw;
}
.structure_button:hover{
    transform: scale(1.2);
}
.structure_button:active{
    background: #d8b6fa;
    color: #22355783;
}
.structure_text{
    color: #000000ce;
    font-size: 80%;
    text-align: center;
}
.other_header{
    color: #803fafb7;
    background: linear-gradient(90deg, #4d2f602e, #2421289d, #484448bf, #130c12b6);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: contrast(1.5);
    text-align: start;
    font-size: 150%;
    font-weight: 500;
    margin-bottom: 2%;
    margin-top: 8%;
}
.other_section{
    display: grid;
    grid-template-columns: 5fr 1fr;
    align-items: center;
    box-sizing: border-box;
}
.text_area{
    margin-bottom: 1%;
    color: #000000ce;
    border: 2px solid #04162135;
    border-radius: 5px; 
    background-color: rgba(202, 229, 220, 0.392);
    width: 100%;
    resize: none;
    height: 20vh;
}
.other_image{
    margin-bottom: 3%;
    color: #d8b6fa;
    font-weight: 900;
    box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.498), 0 0 0 1px rgba(63, 24, 79, 0.172);
    background: linear-gradient(135deg, #0000003c, #22355783);
    border-radius: 2px;
    text-decoration: none;
    transition: 300ms;
    font-size: 90%;
    width: 90%;
    margin-top: 3%;
    opacity: 0.6;
}