.container{
    background: linear-gradient(90deg, #38475145, #3d385122, #4a385134);
    border-top: 2px solid #0000002e;
    display: grid;
    grid-template-columns: 2fr 3fr 1fr;
    align-items: center;
    padding-top: 2%;
    padding-bottom: 2%;
}
.access_img{
    box-shadow:  0 0.4em 1em -0.125em rgb(10 10 10 / 60%), 0 0 0 1px rgb(10 10 10 / 24%);
    width: 90%;
    object-fit: contain;
    margin: auto;
    border-radius: 10px;
    filter: brightness(90%);
}
.description_container{
    text-align: center;
    font-size: 90%;
    padding: 5%;
}
.about_link{
    color: #330f4e;

    transition: 200ms 50ms;
    text-decoration: none;
}
.about_link:hover{
    background: linear-gradient(90deg, #2917349d, #412460, #715871bf, #480d3fdc);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    transform: rotate(1deg);
}
.social_img{
    width: 25px;
    margin: 5% 0 5% 0;
    transition: 100ms;
}
.social_img:hover{
    filter: brightness(1.1);
    transform: scale(1.05);
}
.social_link{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
@media(max-width: 768px){
    .description_container{
        font-size: 70%;
    }
}

@media(max-width: 576px){
    .container{
        grid-template-columns: 5fr 3fr;
    }
    .description_container{
        text-align: left;
        margin-inline: 10%;
    }
    .access_img{
        display: none;
    }
    .social_link{
        flex-direction: row;
        gap: 10%;
    }
    .social_img{
        width: 40%;
    }
}