.container {
    margin-top: 4%;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
}

.header {
    color: #803fafb7;
    background: linear-gradient(90deg, #4d2f602e, #2421289d, #484448bf, #130c12b6);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: contrast(1.5);

    text-align: center;
    font-size: 180%;
    font-weight: 500;
    margin-bottom: 4%;
}
.description_text {
    color: #d8b6fa;
    box-shadow:0 0.4em 1em -0.125em rgba(10, 10, 10, 0.505), 0 0 0 1px rgba(10, 10, 10, 0.081);
    background: linear-gradient(135deg, #0000005e, #22355772);
    border-radius: 5px; 
    padding: 4%;  
    font-size: 100%;
    font-weight: 500;
    margin-bottom: 4%;
}

.image{
    color: #d8b6fa;
    font-weight: 900;
    box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.498), 0 0 0 1px rgba(63, 24, 79, 0.172);
    background: linear-gradient(135deg, #0000003c, #22355783);
    border-radius: 2px;
    text-decoration: none;
    transition: 300ms;
    font-size: 90%;
    width: min-content;
    margin-top: 3%;
    opacity: 0.6;
}
.text{
    margin-bottom: 1%;
    color: #000000ce;
    border: 2px solid #04162135;
    border-radius: 5px; 
    background-color: rgba(202, 229, 220, 0.392);
    width: 90%;
}
.button{
    color: #d8b6fa;
    font-weight: 600;
    box-shadow: 0 0.4em 1em -0.125em rgba(10, 10, 10, 0.498), 0 0 0 1px rgba(63, 24, 79, 0.172);
    background: linear-gradient(135deg, #0000003c, #22355783);
    border-radius: 2px;
    text-decoration: none;
    transition: 300ms;
    font-size: 100%;
    width: fit-content;
    margin-top: 3%;
    margin-bottom: 3%;
    height: fit-content;
    padding-inline: 3%;
}
.button:hover{
    filter: brightness(0.9);
    transform: rotate(-1deg);
}
.button:active{
    transform: scale(1.05);
}
.text:focus-visible{
    outline: 2px solid #7316c54b;
    animation:  3s ease-in-out 0s infinite alternate backwards input;
    box-shadow: 0px 5px 20px -7px rgba(0, 0, 0, 0.777);
}
@keyframes input{
    0%{
        box-shadow: 0px 0px 10px -7px rgba(0, 0, 0, 0.777);
    }
    50%{
        box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.812);
    }
}
.error{
    text-align: center;
    font-size: 80%;
    color: #e84b4b;
    font-weight: 900;
}
.mini_text{
    color: #d8b6fa;
    font-weight: 600;
    text-decoration: none;
    font-size: 80%;
    width: fit-content;
    height: fit-content;
    padding-inline: 3%;
    margin-bottom: 3%;
}
.loading_spinner {
    margin: 6vw auto;
    width: 12vw;
    height: 12vw;
    border: 4px solid transparent;
    border-top: 5px dashed #f3f3f3;
    border-radius: 50%;
    animation: loading_animation 1s linear infinite;
}

@keyframes loading_animation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
